@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.WebsiteLink{
	padding: 5px 10px;
}


.WinputWrap{
	margin: 10px 0px;
}

.Winput{
	font-size: 0.8em;
	padding: 5px 10px;
	height: 35px;
	width: 100%;
	border: 1px solid rgba(204, 204, 204, .3);
}

.Winput:hover{
	border: 1px solid #ccc;
}

.Wbtn{
	margin-top: 5px;
	text-align: center;
	cursor: pointer;
	padding: 8px 0px;
	background-color: #eb9e1a;
	font-size: 0.8em;
}

.Wbtn:hover{
	opacity: 0.8;
}

.wdropdown{

}

.wdropdown>button{

}


.Wlink{
	font-size: 12px;
	cursor: pointer;
}

.Wlink:hover{
	text-decoration: underline;
}

.WErrorInput{
	font-size: 11px;
    padding: 3px 8px;
    background-color: antiquewhite;
	font-family: 'Lato', sans-serif;
}


.Editor {
  	
}


.floatLeft{
	float: left;
}

.floatRight{
	float: right;
}

.floatCenter{
	margin: 0 auto;
	float: none;
}

.zero-padd{
	padding: 0px;
}

.clearBoth{
	clear: both;
}

.actionCursor{
	cursor: pointer;
}

.white-opaque-border{
	border-right: 1px solid rgb(255, 255, 255);
    border-right: 1px solid rgba(255, 255, 255, .5);
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
}

.white-opaque-border-left{
	border-left: 1px solid rgb(255, 255, 255);
    border-left: 1px solid rgba(255, 255, 255, .5);
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
}


.back-mattblack{
	background-color: #262533;
	color: white;
}

.back-orange{
	background-color: #eb9e1a;
	color: black;
}

.hide{
	display: none;
}

.show{
	display: block;
}

.color-white{
	color: white;
}

.color-black{
	color: black;
}


.PlaygroundApp{
	padding-top: 30px;
}

.Services{
	padding: 20px;
	margin-left: 20px;
	box-shadow: 0px 0px 3px #ccc;
}

.SelectedService{
	margin-top: 10px;
	padding: 20px;
	border-top: 1px dotted #ddd;
}

.ServiceName{
	padding-bottom: 20px;
}

.SelectedSubServiceName{
	padding-bottom: 10px;
}

.Output{
	box-shadow: 0px 0px 3px #ccc;
	min-height: 90vh;
	margin-left: 20px;
}


.HeaderContainer{
	min-height: 46px;
	max-height: 46px;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 99999;
	box-shadow: 2px 2px 2px #ccc;
	padding-top: 8px;
}

.HeaderMenu{
	min-height: 30px;
}

.HeaderLogo{
	font-size: 19px;
}


.LeftMenuContainer{
	position: absolute;
	top: 46px;
	z-index: 999999;
	box-shadow: 2px 2px 2px #aaa;
}

.LeftMenuDiv{
	height: 90vh;
	padding-top: 20px;
}


.WebketchComponent{
	
}


.UserApp{
	font-family: 'Mukta', sans-serif;
}

.UserAppPromo{
	background-color: #eb9e1a;
	color: black;
	min-height: 100vh;
}

.UserAppSlogan{
	font-size: 32px;
}

.WebketchHeading{
	font-size: 48px;
	margin-top: 240px;
}

.UserAppContainer{
	margin-top: 180px;
	font-family: 'Noto Sans JP', sans-serif;
}

.UserAppHead{
	font-size: 1.2em;
	padding-bottom: 12px;
	margin-bottom: 20px;
}

.UserAppLinks{
	margin-top: 20px;
}



